.chop {
  padding: 4rem 0;

  @media (min-width: 1024px) {
    padding: 7rem 0;
  }

  @media (min-width: 1024px) {
    padding: 11rem 0;
  }

  &__inner {
    position: relative;
    z-index: 1;
    max-width: 113rem;

    &::after {
      position: absolute;
      content: "";
      z-index: -1;
      top: -0.1rem;
      left: -0.1rem;
      right: -0.2rem;
      bottom: -0.1rem;
      background: #00E8AD;
      background: linear-gradient(180deg, #00E8AD, #201D54);
      border-radius: 2.5rem;
    }
  }

  &__text {
