    // max-width: 128rem;
    // width: 113rem;
    padding: 2.2rem 1.5rem 2.8rem 1.5rem;
    background: linear-gradient(253.84deg, rgba(48, 44, 116, 0) 2.2%, rgba(68, 64, 135, 0.4) 12.2%, rgba(48, 44, 116, 0) 61.23%), #201D54;
    box-shadow: inset 0 1rem 2rem rgba(255, 255, 255, 0.25);
    border-radius: 2.5rem;
    // background: url("../img/icons/chop-bg-xs.svg");
    // background-repeat: no-repeat;
    // background-position: 0 0;
    // background-size: cover;

    @media (min-width: 1024px) {
      padding: 6.9rem 6.2rem 8.3rem 6.2rem;

    }

    @media (min-width: 1440px) {
