    h2,
    p {
      font-family: 'Apple SD Gothic Neo';
      font-style: normal;
      color: var(--white-main);
    }

    h2 {
      margin-bottom: 1.5rem;
      text-align: center;
      @include AppleSDGothicNeoExtraBold();
      font-size: 2.6rem;
      line-height: 3.1rem;

      @media (min-width: 500px) {
        font-size: 2.8rem;
        line-height: 3.3rem;
      }

      @media (min-width: 650px) {
        font-size: 3rem;
        line-height: 3.5rem;
      }

      @media (min-width: 750px) {
        font-size: 3.2rem;
        line-height: 3.7rem;
      }


      @media (min-width: 1200px) {
        margin-bottom: 3.5rem;
        font-size: 5.2rem;
        line-height: 6.2rem;
      }
    }

    p {
      @include AppleSDGothicNeoLight();
      font-size: 1.4rem;
      line-height: 125%;

      @media (min-width: 500px) {
        font-size: 1.6rem;
      }

      @media (min-width: 750px) {
        font-size: 1.8rem;
      }

      @media (min-width: 900px) {
        font-size: 2rem;
      }

      @media (min-width: 1024px) {
        font-size: 2.2rem;
      }

      &:not(:last-of-type) {
        margin-bottom: 1.5rem;

        @media (min-width: 750px) {
          margin-bottom: 2rem;
        }

        @media (min-width: 900px) {
          margin-bottom: 2.5rem;
        }

        @media (min-width: 1200px) {
          margin-bottom: 3rem;
        }
      }

      &:last-of-type {
        padding-bottom: 2rem;

        @media (min-width: 750px) {
          padding-bottom: 4rem;
        }

        @media (min-width: 900px) {
          padding-bottom: 6rem;
        }

        @media (min-width: 1024px) {
          padding-bottom: 7rem;
        }

        @media (min-width: 1200px) {
          padding-bottom: 8.3rem;
        }
      }
    }
  }
}